<template>
	<view class="V_home">
		<!-- 头部 -->
		<view class="V_home_header">
			<!-- 左侧日期 -->
			<view class="left_time">
				<view class="day_font">
					{{day}}
				</view>
				<view class="year_font">
					{{year}}
				</view>
				<view class="month_font">
					{{wordMounth}}
				</view>
			</view>
			<!-- 右侧时间 -->
			<view class="right_time">
				{{hours}}:{{miuite}}
			</view>
		</view>
		<!-- 卡片列表 -->
		<HM-dragSorts :list="cardData" :isLongTouch="false" :isAutoScroll="true" :feedbackGeneratorState="true"
			:rowHeight="180" @change="change" @confirm="confirm" @onclick="onclick">
			<template  slot="rowContent" slot-scope="{row}">
				<!-- 标题 -->
				<view class="card_title" :style="{backgroundColor:row.titleColor}" @click="titleClick(row.title)">
					<text v-if="titleFlag !== row.title">{{row.title}}</text>
					<u--input
						v-if="titleFlag === row.title"
					    :value = "row.title"
					    border="none"
						:focus="true"
						maxlength="15"
						@change="titleChange"
						@blur="titleBlur"
					  ></u--input>
				</view>
				<!-- 内容 -->
				<view class="wrap">
					<u-row v-for="(item,index) in row.data" :key="index">
						<u-col span="6" @click="textClick(item.content)">
							<view class="card_text" v-if="testFlag !== item.content">{{item.content}}</view>
							<u--input
								v-if="testFlag === item.content"
							    :value = "item.content"
							    border="none"
								fontSize = "20px"
							    clearable
								class="card_input"
								@change="inputChange"
								@blur="inputBlur"
							  ></u--input>
						</u-col>
						<u-col span="6">
								<label class="radio card_radio">
									<radio value="" />
								</label>
						</u-col>
					</u-row>
				</view>
				
			</template>
		</HM-dragSorts>


	</view>
</template>

<script>
	import Data from '../../external_files/date.js'
	import exchange from '../../components/pretty-exchange/pretty-exchange.vue'
	export default {
		data() {
			return {
				count: 0,
				changeData:'',
				// 内容框判断
				testFlag:false,
				// 标题框判断
				titleFlag:false,
				hours: '',
				miuite: '',
				day: '',
				year: '',
				month: '',
				wordMounth: '',
				cardData:[],
				list: [{
						name: '1',
						data:['清单','学习','吃饭','打地鼠','喝可乐','睡觉觉'],
						emit:'To Do',
						emitColor:'skyblue'
					},
					{
						name: '2',
						data:['清单2','学习2'],
						emit:'To Do2',
						emitColor:'#f05000'
					},
					{
						name: '3',
						data:['清单3','学习3'],
						emit:'To Do3',
						emitColor:'black'
					},
					{
						name: '4',
						data:['清单4','学习4'],
						emit:'To Do4',
						emitColor:'gray'
					},
					{
						name: '5',
						data:['清单5','学习5'],
						emit:'To Do5',
						emitColor:'orange'
					}

				]
			}
		},
		components: {
			exchange
		},
		created() {
			// 获取时间
			this.getTime()
			var that = this
			// 发起数据库请求
			this.dbInit()
			
		},
		methods: {
			getTime() {
				const {
					year,
					month,
					day,
					hour,
					miuite,
					timestrap,
					wordMounth
				} = Data()
				this.hours = hour
				this.miuite = miuite
				this.day = day
				this.year = year
				this.month = month
				this.wordMounth = wordMounth
			},
			onclick(e) {
				// console.log('=== onclick start ===');
				// console.log("被点击行: " + JSON.stringify(e.value));
				// console.log("被点击下标: " + JSON.stringify(e.index));
				// console.log('=== onclick end ===');
			},
			change(e) {
				// console.log('=== change start ===');
				// console.log("被拖动行: " + JSON.stringify(e.moveRow));
				// console.log('原始下标：', e.index);
				// console.log('移动到：', e.moveTo);
				// console.log('=== change end ===');
			},
			confirm(e) {
				// console.log("this.list: ", this.list);
				// console.log("e.list: ", e.list);
				// console.log('=== confirm start ===');
				// console.log("被拖动行: " + JSON.stringify(e.moveRow));
				// console.log('原始下标：', e.index);
				// console.log('移动到：', e.moveTo);
				// console.log('=== confirm end ===');
			},
			// 卡片内容框点击
			textClick (item) {
				// 单击事件
				// this.count++
				// let times = setTimeout(()=>{
				// 	this.count > 1
				// 	// 双击事件
				// 	? this.testFlag = item
				// 	// 单击事件 不处理
				// 	: ''
				// 	clearTimeout(times)
				// 	this.count = 0
				this.testFlag = item
				// },500)
				
			},
			// input 修改值
			inputChange (e) {
				this.changeData = e
			},
			// input 框失焦
			inputBlur () {
				// 失焦获取改变的值发起请求修改
				console.log(this.changeData)
				this.testFlag = false
			},
			// 卡片标题框点击
			titleClick (e) {
				this.count++
				let times = setTimeout(()=>{
					this.count > 1
					// 双击事件
					? this.titleFlag = e
					// 单击事件 不处理
					: ''
					clearTimeout(times)
					this.count = 0
				},500)
			},
			// 卡片标题变值
			titleChange() {
				
			},
			// 卡片标题失焦
			titleBlur() {
				this.titleFlag = false
			},
			// 数据库初始化请求数据
			async dbInit() {
				const db = uniCloud.database()
				let res = await db.collection('todo_list').where({user :'vike'}).get()
				console.log(res)
				if (res.result.code === 0) {
					// 接口请求成功
					this.cardData = res.result.data[0].data
				}
			}
		}
	}
</script>

<style>
	.V_home {
		width: 100vw;
		height: 100vh;
		background-color: #E1E1E1;
		overflow-x: hidden;
		overflow-y: auto;
	}

	.V_home_header {
		padding: 40rpx;
		height: 8vh;
		width: 100vw;
	}

	.left_time {
		position: absolute;
	}

	.day_font {
		font-size: 80rpx;

	}

	.year_font {
		font-size: 35rpx;
		position: absolute;
		left: 120%;
		top: 50%;
		color: gray;
	}

	.month_font {
		font-size: 30rpx;
		position: absolute;
		left: 120%;
		top: 10%;
		color: gray;
	}

	.right_time {
		font-size: 80rpx;
		position: absolute;
		right: 6%;
	}

	
	.card_radio {
/* 		position: absolute;
		right: 15%; */
		padding-top: 50rpx; 
		/* margin-top: 45rpx; */
		margin-right: 10rpx;
		transform: translate(30%);
	}
	
	.card_text {
		/* position: absolute; */
		margin-left: 15px;
		/* margin-top: 45rpx; */
		padding-top: 50rpx;
		font-size: 10px;
	}
	.wrap {
		height: 160px;
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.card_input {
		margin-left: 16px;
		margin-top: 50rpx;
	}
	
	.card_title {
		width: auto;
		padding: 20rpx 30rpx;
		background-color: skyblue;
		color: #FFFFFF;
		position: absolute;
		border-radius: 20rpx;
		left: 18%;
		font-size: 35rpx;
		transform: translateY(-50%);
	}
	
</style>
